/** ä¸€é”®åˆ›å»ºä¸–ç•Œä¹¦å¹¶ç»‘å®šåˆ°å½“å‰èŠå¤© */
export async function createWorldbookForChat(name?: string): Promise<string> {
  const charName = getCurrentCharacterName();
  const worldbookName = name || `${charName || 'æœªçŸ¥'}[è‡ªåŠ¨æ€»ç»“]`;

  const existingNames = getWorldbookNames();
  if (!existingNames.includes(worldbookName)) {
    await createWorldbook(worldbookName, []);
    console.log(`[è‡ªåŠ¨æ€»ç»“] å·²åˆ›å»ºä¸–ç•Œä¹¦: ${worldbookName}`);
  }

  // ç»‘å®šåˆ°èŠå¤©æ–‡ä»¶ï¼ˆåŸç”Ÿæœºåˆ¶ï¼‰
  await rebindChatWorldbook('current', worldbookName);

  console.log(`[è‡ªåŠ¨æ€»ç»“] å·²ç»‘å®šä¸–ç•Œä¹¦: ${worldbookName}`);
  return worldbookName;
}

