// ========== [data] å…ƒæ•°æ®æ¡ç›®æ“ä½œ ==========

const DATA_ENTRY_NAME = '[data]';

/** ç¡®ä¿ä¸–ç•Œä¹¦ä¸­å­˜åœ¨ [data] æ¡ç›®ï¼Œä¸å­˜åœ¨åˆ™åˆ›å»º */
export async function ensureDataEntry(): Promise<void> {
  const worldbookName = getWorldbookName();
  if (!worldbookName || !worldbookExists()) return;

  const worldbook = await getWorldbook(worldbookName);
  const existing = worldbook.find(e => e.name === DATA_ENTRY_NAME);

  if (!existing) {
    const defaultData = MetaData.parse({});
    await createWorldbookEntries(worldbookName, [
      {
        ...ENTRY_DEFAULTS,
        enabled: false,
        name: DATA_ENTRY_NAME,
        content: JSON.stringify(defaultData),
      },
    ]);
    console.log('[è‡ªåŠ¨æ€»ç»“] å·²åˆ›å»º [data] å…ƒæ•°æ®æ¡ç›®');
  }
}

/** ä»ä¸–ç•Œä¹¦ [data] æ¡ç›®è¯»å–è¿è¡Œæ—¶å…ƒæ•°æ® */
export async function getMetaData(): Promise<MetaDataType> {
  const worldbookName = getWorldbookName();
  if (!worldbookName || !worldbookExists()) {
    return MetaData.parse({});
  }

  const worldbook = await getWorldbook(worldbookName);
  const entry = worldbook.find(e => e.name === DATA_ENTRY_NAME);

  if (!entry) {
    return MetaData.parse({});
  }

  try {
    const raw = JSON.parse(entry.content);
    return MetaData.parse(raw);
  } catch (e) {
    console.error('[è‡ªåŠ¨æ€»ç»“] è§£æ [data] æ¡ç›®å¤±è´¥ï¼Œä½¿ç”¨é»˜è®¤å€¼:', e);
    return MetaData.parse({});
  }
}

/** å°†è¿è¡Œæ—¶å…ƒæ•°æ®ä¿å­˜åˆ°ä¸–ç•Œä¹¦ [data] æ¡ç›® */
export async function saveMetaData(data: MetaDataType): Promise<void> {
  const worldbookName = getWorldbookName();
  if (!worldbookName || !worldbookExists()) {
    console.warn('[è‡ªåŠ¨æ€»ç»“] ä¸–ç•Œä¹¦ä¸å­˜åœ¨ï¼Œè·³è¿‡ä¿å­˜å…ƒæ•°æ®');
    return;
  }

  const content = JSON.stringify(data);

  await updateWorldbookWith(worldbookName, wb => {
    const entry = wb.find(e => e.name === DATA_ENTRY_NAME);
    if (entry) {
      entry.content = content;
    }
    return wb;
  });
}

// ========== å°æ€»ç»“æ¡ç›®æ“ä½œ ==========

/** è·å–æŒ‡å®šæ¥¼å±‚çš„å°æ€»ç»“æ¡ç›® */
export async function getMiniSummaryEntry(message_id: number): Promise<WorldbookEntry | undefined> {
  const name = getWorldbookName();
  if (!name || !worldbookExists()) return undefined;

  const worldbook = await getWorldbook(name);
  const entryName = `[å°æ€»ç»“-æ¥¼å±‚${message_id}]`;
  return worldbook.find(e => e.name === entryName);
}

/** åˆ›å»ºæˆ–æ›¿æ¢æŒ‡å®šæ¥¼å±‚çš„å°æ€»ç»“æ¡ç›® */
export async function upsertMiniSummaryEntry(message_id: number, content: string): Promise<void> {
  const worldbookName = getWorldbookName();
  if (!worldbookName || !worldbookExists()) {
    console.warn('[è‡ªåŠ¨æ€»ç»“] ä¸–ç•Œä¹¦ä¸å­˜åœ¨ï¼Œè·³è¿‡å†™å…¥å°æ€»ç»“');
    return;
  }

  const entryName = `[å°æ€»ç»“-æ¥¼å±‚${message_id}]`;
  const worldbook = await getWorldbook(worldbookName);
  const existing = worldbook.find(e => e.name === entryName);

  if (existing) {
    // å·²å­˜åœ¨åˆ™æ›¿æ¢ content
    await updateWorldbookWith(worldbookName, wb => {
      const entry = wb.find(e => e.name === entryName);
      if (entry) {
        entry.content = content;
      }
      return wb;
    });
  } else {
    // ä¸å­˜åœ¨åˆ™æ–°å»º
    const settings = getSettings();
    await createWorldbookEntries(worldbookName, [
      {
        ...ENTRY_DEFAULTS,
        name: entryName,
        content,
        position: {
          type: 'at_depth',
          role: 'system',
          depth: settings.mini_summary_depth,
          order: getMiniSummaryOrder(message_id, settings.mini_summary_start_order),
        },
      },
    ]);
  }
}

/** åˆ›å»ºç©ºå ä½å°æ€»ç»“æ¡ç›®ï¼ˆåŒæ­¥é˜¶æ®µä½¿ç”¨ï¼‰ */
