/** è·å–æ‰€æœ‰æœªå½’æ¡£çš„å°æ€»ç»“æ¡ç›® */
export async function getUnarchivedMiniSummaries(): Promise<WorldbookEntry[]> {
  const name = getWorldbookName();
  if (!name || !worldbookExists()) return [];

  const worldbook = await getWorldbook(name);
  const metadata = await getMetaData();

  // æ”¶é›†å·²å½’æ¡£çš„æ¥¼å±‚ ID
  const archivedIds = new Set<number>();
  for (const vol of metadata.volumes) {
    for (let id = vol.start_message_id; id <= vol.end_message_id; id++) {
      archivedIds.add(id);
    }
  }

  return worldbook.filter(entry => {
    const match = entry.name.match(/^\[å°æ€»ç»“-æ¥¼å±‚(\d+)\]$/);
    if (!match) return false;
    const id = parseInt(match[1]);
    return !archivedIds.has(id);
  });
}

// ========== enabled åŒæ­¥ ==========

/** æ ¹æ®æ¥¼å±‚å¯è§æ€§å’Œå½’æ¡£çŠ¶æ€åŒæ­¥æ‰€æœ‰å°æ€»ç»“çš„ enabled */
export async function syncMiniSummaryEnabled(): Promise<void> {
  const name = getWorldbookName();
  if (!name || !worldbookExists()) return;

  const settings = getSettings();
  const lastId = getLastMessageId();
  const visibleThreshold = lastId - settings.visible_floors + 1;
  const metadata = await getMetaData();

  // æ”¶é›†å·²å½’æ¡£çš„æ¥¼å±‚ ID èŒƒå›´
  const archivedIds = new Set<number>();
  for (const vol of metadata.volumes) {
    for (let id = vol.start_message_id; id <= vol.end_message_id; id++) {
      archivedIds.add(id);
    }
  }

  await updateWorldbookWith(name, worldbook => {
    for (const entry of worldbook) {
      const match = entry.name.match(/^\[å°æ€»ç»“-æ¥¼å±‚(\d+)\]$/);
      if (!match) continue;
      const id = parseInt(match[1]);

      if (id >= visibleThreshold) {
        // å¯è§æ¥¼å±‚ â†’ ä¸æ³¨å…¥
        entry.enabled = false;
      } else if (archivedIds.has(id)) {
        // å·²å½’æ¡£ â†’ ä¸æ³¨å…¥
        entry.enabled = false;
      } else {
        // å·²éšè—ä¸”æœªå½’æ¡£ â†’ æ³¨å…¥
        entry.enabled = true;
      }
    }
    return worldbook;
  });
}

// ========== å·æ¡ç›®æ“ä½œ ==========

/** åˆ›å»ºå·æ¡ç›®å¹¶å…³é—­å¯¹åº”èŒƒå›´å†…çš„å°æ€»ç»“ */
export async function createVolumeEntry(
  volume: number,
  start_id: number,
  end_id: number,
  content: string
): Promise<void> {
  const worldbookName = getWorldbookName();
  if (!worldbookName || !worldbookExists()) {
    console.warn('[è‡ªåŠ¨æ€»ç»“] ä¸–ç•Œä¹¦ä¸å­˜åœ¨ï¼Œè·³è¿‡åˆ›å»ºå·æ¡ç›®');
    return;
  }

  const entryName = `[å·${volume}-æ¥¼å±‚${start_id}~æ¥¼å±‚${end_id}]`;

  await updateWorldbookWith(worldbookName, worldbook => {
    // 1. å…³é—­è¯¥èŒƒå›´å†…çš„å°æ€»ç»“æ¡ç›®
    for (const entry of worldbook) {
      const match = entry.name.match(/^\[å°æ€»ç»“-æ¥¼å±‚(\d+)\]$/);
      if (!match) continue;
      const id = parseInt(match[1]);
      if (id >= start_id && id <= end_id) {
        entry.enabled = false;
      }
    }

    // 2. æ·»åŠ å·æ¡ç›®
    const settings = getSettings();
    worldbook.push({
      ...ENTRY_DEFAULTS,
      enabled: true,
      name: entryName,
      content,
      position: {
        type: 'at_depth',
        role: 'system',
        depth: settings.volume_summary_depth,
        order: getVolumeOrder(volume, settings.volume_start_order),
      },
    } as WorldbookEntry);

    return worldbook;
  });

  // 3. æ›´æ–°å…ƒæ•°æ®
  const meta = await getMetaData();
  meta.current_volume = volume + 1;
  meta.volumes.push({ volume, start_message_id: start_id, end_message_id: end_id });
  await saveMetaData(meta);
}

/** è·å–æ‰€æœ‰å·²æœ‰å·æ¡ç›® */
export async function getVolumes(): Promise<WorldbookEntry[]> {
  const name = getWorldbookName();
  if (!name || !worldbookExists()) return [];

  const worldbook = await getWorldbook(name);
  return worldbook.filter(entry => /^\[å·\d+-æ¥¼å±‚\d+~æ¥¼å±‚\d+\]$/.test(entry.name));
}
