/** ç”Ÿæˆå•æ¡æ¶ˆæ¯çš„å°æ€»ç»“å†…å®¹ */
export async function generateMiniSummaryContent(message_id: number): Promise<string> {
  const settings = getSettings();
  const worldbookName = getWorldbookName();
  if (!worldbookName) {
    throw new Error('[è‡ªåŠ¨æ€»ç»“] æœªç»‘å®šä¸–ç•Œä¹¦ï¼Œæ— æ³•ç”Ÿæˆå°æ€»ç»“');
  }
  const worldbook = await getWorldbook(worldbookName);

  // è·å–å‰ 2 ä¸ªå°æ€»ç»“ä½œä¸ºä¸Šä¸‹æ–‡
  const allMiniEntries = worldbook
    .filter(e => e.name.match(/^\[å°æ€»ç»“-æ¥¼å±‚(\d+)\]$/))
    .map(e => ({
      id: parseInt(e.name.match(/æ¥¼å±‚(\d+)/)![1]),
      content: e.content,
    }))
    .filter(e => e.id < message_id)
    .sort((a, b) => a.id - b.id)
    .slice(-2);

  const context = allMiniEntries.map(e => e.content).join('\n');

  // è·å–å¹¶æ¸…æ´—å½“å‰æ¥¼å±‚æ¶ˆæ¯
  const messages = getChatMessages(message_id);
  if (messages.length === 0) {
    throw new Error(`[è‡ªåŠ¨æ€»ç»“] æ¥¼å±‚ ${message_id} ä¸å­˜åœ¨`);
  }
  const rawMessage = messages[0].message;

  // è·³è¿‡ç©ºæ¶ˆæ¯
  if (!rawMessage || rawMessage.trim() === '') {
    return 'ï¼ˆç©ºæ¶ˆæ¯ï¼‰';
  }

  // åº”ç”¨é…’é¦†æ­£åˆ™è¿‡æ»¤ï¼ˆæ ¹æ®æ¶ˆæ¯è§’è‰²ç¡®å®š sourceï¼‰
  const source = messages[0].role === 'user' ? 'user_input' : 'ai_output';
  const regexedMessage = formatAsTavernRegexedString(rawMessage, source, 'prompt');

  // æå–æ•è·æ ‡ç­¾å†…å®¹
  const extracted = extractTaggedContent(
    regexedMessage,
    settings.capture_start_tag,
    settings.capture_end_tag
  );

  const cleaned = cleanMessage(extracted, settings);
  const prompt = getMiniSummaryPrompt(cleaned, context);

  return await callAI(prompt.system, prompt.user, settings);
}

/** å¤„ç†å°æ€»ç»“ä»»åŠ¡ - ç”Ÿæˆå†…å®¹å¹¶å†™å…¥å·²åˆ›å»ºçš„æ¡ç›® */
export async function handleMiniSummary(message_id: number): Promise<void> {
  // ä¸–ç•Œä¹¦ä¸å­˜åœ¨åˆ™è·³è¿‡
  if (!worldbookExists()) {
    console.warn('[è‡ªåŠ¨æ€»ç»“] ä¸–ç•Œä¹¦ä¸å­˜åœ¨ï¼Œè·³è¿‡å°æ€»ç»“ç”Ÿæˆ');
    return;
  }

  // ç”Ÿæˆå°æ€»ç»“å†…å®¹
  const summary = await generateMiniSummaryContent(message_id);

  // å°†ç»“æœå†™å…¥å·²å­˜åœ¨çš„æ¡ç›®ï¼ˆupsert å¤„ç†ï¼Œè‹¥æ¡ç›®ä¸å­˜åœ¨ä¹Ÿä¼šåˆ›å»ºï¼‰
  await upsertMiniSummaryEntry(message_id, summary);

  // æ›´æ–°å…ƒæ•°æ®
  const meta = await getMetaData();
  meta.last_processed_message_id = message_id;
  await saveMetaData(meta);
}

