/** æ£€æŸ¥æ˜¯å¦åº”è¯¥è§¦å‘å¤§æ€»ç»“ */
export async function shouldTriggerVolumeSummary(): Promise<boolean> {
  const settings = getSettings();
  const unarchivedEntries = await getUnarchivedMiniSummaries();

  if (unarchivedEntries.length === 0) return false;

  // æ£€æŸ¥ token é˜ˆå€¼ï¼ˆç®€å•ç”¨å­—ç¬¦æ•°ä¼°ç®— token æ•°ï¼Œä¸­æ–‡çº¦ 1 å­— â‰ˆ 1-2 tokenï¼‰
  const totalContent = unarchivedEntries.map(e => e.content).join('');
  const estimatedTokens = totalContent.length; // ç²—ç•¥ä¼°ç®—

  if (estimatedTokens > settings.volume_token_threshold) {
    return true;
  }

  // AI åˆ¤æ–­æ˜¯å¦ä¸€å·å·²å®Œç»“
  try {
    const miniSummaries = unarchivedEntries.map(e => {
      const match = e.name.match(/æ¥¼å±‚(\d+)/);
      const id = match ? match[1] : '?';
      return `æ‘˜è¦${id}\n${e.content}`;
    });
    const prompt = getVolumeCompletionCheckPrompt(miniSummaries);
    const result = await callAI(prompt.system, prompt.user, settings);
    // æ£€æŸ¥å›ç­”ä¸­æ˜¯å¦åŒ…å« 114514ï¼ˆå®Œç»“ï¼‰æˆ– 1919810ï¼ˆæœªå®Œç»“ï¼‰
    if (result.includes('114514')) return true;
    if (result.includes('1919810')) return false;
    // éƒ½ä¸åŒ…å«æ—¶é»˜è®¤ä¸è§¦å‘
    console.warn('[è‡ªåŠ¨æ€»ç»“] å·å®Œç»“æ£€æµ‹è¿”å›äº†æ„å¤–å†…å®¹:', result);
    return false;
  } catch (e) {
    console.error('[è‡ªåŠ¨æ€»ç»“] å·å®Œç»“æ£€æµ‹å¤±è´¥:', e);
    return false;
  }
}

/** ç”Ÿæˆå¤§æ€»ç»“å†…å®¹ */
export async function generateVolumeSummaryContent(
  mini_summaries: WorldbookEntry[]
): Promise<string> {
  const settings = getSettings();
  const volumes = await getVolumes();

  const miniContents = mini_summaries.map(e => {
    const match = e.name.match(/æ¥¼å±‚(\d+)/);
    const id = match ? match[1] : '?';
    return `æ‘˜è¦${id}\n${e.content}`;
  });
  const previousVolumeContents = volumes
    .sort((a, b) => {
      const aVol = parseInt(a.name.match(/å·(\d+)/)![1]);
      const bVol = parseInt(b.name.match(/å·(\d+)/)![1]);
      return aVol - bVol;
    })
    .map(e => e.content);

  const prompt = getVolumeSummaryPrompt(miniContents, previousVolumeContents);
  return await callAI(prompt.system, prompt.user, settings);
}

/** æ‰§è¡Œå®Œæ•´çš„å¤§æ€»ç»“æµç¨‹ */
export async function performVolumeSummary(): Promise<void> {
  // ä¸–ç•Œä¹¦ä¸å­˜åœ¨åˆ™è·³è¿‡
  if (!worldbookExists()) {
    console.warn('[è‡ªåŠ¨æ€»ç»“] ä¸–ç•Œä¹¦ä¸å­˜åœ¨ï¼Œè·³è¿‡å¤§æ€»ç»“ç”Ÿæˆ');
    return;
  }

  const meta = await getMetaData();
  const unarchivedEntries = await getUnarchivedMiniSummaries();

  if (unarchivedEntries.length === 0) return;

  // å…ˆæ£€æŸ¥æ˜¯å¦æ»¡è¶³å¤§æ€»ç»“æ¡ä»¶
  const shouldTrigger = await shouldTriggerVolumeSummary();
  if (!shouldTrigger) return;

  // æå–æ¥¼å±‚ ID èŒƒå›´
  const ids = unarchivedEntries
    .map(e => parseInt(e.name.match(/æ¥¼å±‚(\d+)/)![1]))
    .sort((a, b) => a - b);

  let start_id = ids[0];
  let end_id = ids[ids.length - 1];

  // ç”Ÿæˆå¤§æ€»ç»“
  const content = await generateVolumeSummaryContent(unarchivedEntries);

  // å°è¯•æå–è®¾å®šçš„èŒƒå›´
  const match = content.match(/æ‘˜è¦(\d+)-æ‘˜è¦(\d+)/);
  if (match) {
    const parsedStart = parseInt(match[1]);
    const parsedEnd = parseInt(match[2]);
    if (parsedStart <= parsedEnd) {
      start_id = parsedStart;
      end_id = parsedEnd;
    } else {
      console.warn(
        `[è‡ªåŠ¨æ€»ç»“] AI è¾“å‡ºçš„èŒƒå›´(æ‘˜è¦${parsedStart}-æ‘˜è¦${parsedEnd})æ— æ•ˆï¼Œå›é€€ä½¿ç”¨å…¨èŒƒå›´`
      );
    }
  } else {
    console.warn('[è‡ªåŠ¨æ€»ç»“] å·æ€»ç»“ä¸­æœªæ‰¾åˆ°æœ‰æ•ˆçš„é¦–å°¾èŒƒå›´æ ‡è®°ï¼Œå›é€€ä½¿ç”¨å…¨èŒƒå›´');
  }

  // æ¸…é™¤ç»“å°¾çš„æŒ‡å®šæ ‡è®°è¡Œå¹¶å­˜å…¥
  const finalContent = content.replace(/\s*æ‘˜è¦\d+-æ‘˜è¦\d+\s*$/, '').trim();

  // åˆ›å»ºå·æ¡ç›®å¹¶å…³é—­å¯¹åº”å°æ€»ç»“
  await createVolumeEntry(meta.current_volume, start_id, end_id, finalContent);

  console.log(`[è‡ªåŠ¨æ€»ç»“] å·²å½’æ¡£å·${meta.current_volume}: æ¥¼å±‚${start_id}~æ¥¼å±‚${end_id}`);
}
